:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

main {
  position: relative;
}

.gallery {
  z-index: 0;
  flex-flow: wrap;
  justify-content: center;
  gap: 24px;
  max-width: 1128px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.gallery-item {
  width: 360px;
  height: 200px;
  position: relative;
}

.gallery-image:hover {
  transform: scale(1.03);
}

.gallery-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery-link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery-link:hover {
  cursor: zoom-in;
  cursor: url("icons.b5b314d4.svg"), auto;
}

li {
  list-style-type: none;
}

.basicLightbox {
  z-index: 9999;
  background: #2e2f42cc;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox__placeholder {
  justify-content: center;
  align-items: center;
  width: 1112px;
  height: 640px;
  display: flex;
}

.basicLightbox__placeholder img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  display: block;
}
